<template>
  <div class="page">
    <div class="top">
      <div class="text">
        <h1 @dblclick="fullscreen">新盛和阳果蔬生鲜市场今日菜价</h1>
        <div class="category">
          <li :class="{active: item == allCategory[currentCategoryIndex]}" @click="clickCategory(index)"
            v-for="(item, index) of allCategory" :key='index'>
            {{item.name}}
            <template v-if="item == allCategory[currentCategoryIndex] && currentTotalPage > 1">
              {{currentPage}}/{{currentTotalPage}}
            </template>
          </li>
        </div>
      </div>
    </div>
    <div class="wrapper">
      <div class="table table1">
        <template v-for="(item, index) of currentShowProducts">
          <div class="cell" @click="open(item)" v-if="!isOdd(index)" :key='index'>
            <div class="l">
              <span>{{item.name}}</span>
            </div>
            <div class="m"> </div>
            <div class="r">
              <span>{{item.price || '-'}}<i>{{item.unit}}</i></span>
            </div>
          </div>
        </template>
      </div>
      <div class="table table2">
        <template v-for="(item, index) of currentShowProducts">
          <div class="cell" @click="open(item)" v-if="isOdd(index)" :key='index'>
            <div class="l">
              <span>{{item.name}}</span>
            </div>
            <div class="m"> </div>
            <div class="r">
              <span>{{item.price || '-'}}<i>{{item.unit}}</i></span>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  let timer;
  let timerout;
  let selfSwitchInterval = 5000
  let reSwitchInterval = 10000
  import Category from "@/request/Category"
  const categoryRequest = new Category();
  import Product from "@/request/Product"
  const productRequest = new Product();
  export default {
    async created() {
      await this.getCategory()
      await this.getProducts()
      this.startLoop()
      this.showNext(0)
    },
    data() {
      return {
        allCategory: [],
        allProducts: [],
        currentCategoryIndex: 0,
        currentShowProducts: [],
        currentTotalPage: 0,
        currentPage: 0,
      }
    },
    methods: {
      async getCategory() {
        let res = await categoryRequest.getAll()
        this.allCategory = res.rows
      },
      async getProducts() {
        this.allProducts = []
        for (const key in this.allCategory) {
          let data = {
            category_id: this.allCategory[key].id
          }
          let res = await productRequest.get(data)
          this.allProducts.push(res.rows)
        }
      },
      startLoop() {
        let categoryLength = this.allCategory.length
        clearInterval(timer)
        timer = setInterval(() => {
          if (this.currentPage == this.currentTotalPage) {
            this.currentCategoryIndex++
            this.currentPage = 0
            if (this.currentCategoryIndex >= categoryLength) {
              this.currentCategoryIndex = 0
            }
          }
          this.showNext(this.currentCategoryIndex)
        }, selfSwitchInterval);
      },
      showProduct() {
        let currentProductsOfCategory = this.cutBy(this.allProducts[this.currentCategoryIndex], 20)
        this.currentTotalPage = currentProductsOfCategory.length
      },
      cutBy(arr, number) {
        arr = Array.from(arr)
        let tempArr = []
        while (arr.length !== 0) {
          let temp = arr.splice(0, number);
          tempArr.push(temp)
        }
        return tempArr
      },
      isOdd(num) {
        return num % 2 === 1 ? true : false
      },
      clickCategory(index) {
        this.showNext(index)
        clearInterval(timer)
        
        clearTimeout(timerout)
        timerout = setTimeout(() => {
          
          this.startLoop()
        }, reSwitchInterval)
      },
      showNext(index) {
        
        let currentProductsOfCategory = this.cutBy(this.allProducts[index], 20)
        this.currentTotalPage = currentProductsOfCategory.length
        
        
        if (this.currentCategoryIndex == index) {
          this.currentPage++
          if (this.currentPage > this.currentTotalPage) {
            this.currentPage = 1
          }
        }
        else {
          this.currentCategoryIndex = index
          this.currentPage = 1
        }
        this.currentShowProducts = currentProductsOfCategory[this.currentPage - 1]
      },
      async open(item) {
        clearInterval(timer)
        try {
          let para = `
          <h1 style="padding-bottom: 10px;">${item.name}</h1>
          <p>价格: ${item.price}元/斤</p>
          <p>进货渠道: ${item.purchase_channels}</p>
          <p>供应商: ${item.suppliers}</p>
          <p>质量检测: ${item.qcpass ? "合格" : "不合格"}</p>
          <p>新盛新盛和阳果蔬生鲜市场食材选自优质供货渠道,<br>新鲜安全有保障</p>
          <p>${item.note}</p>
          <p style="margin-top:10px;"><img src="./img/logo.png" style="width: 200px;"></p>
          `
          await this.$alert(para, {
            dangerouslyUseHTMLString: true,
            center: true,
          });
          this.startLoop()
        }
        catch (e) {
          this.startLoop()
        }
      },
      beforeClose() {
        this.startLoop()
      },
      fullscreen() {
        let element = document.documentElement;
        if(element.requestFullscreen) {
          element.requestFullscreen()
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  * {
    user-select: none;
  }
  .page {
    height: 100%;
  }

  .top {
    height: 120px;
    display: flex;

    .text {
      margin: auto;
    }

    h1 {
      text-align: center;
      font-size: 40px;
      color:rgb(33,172,55);
      padding-bottom: 10px;
    }

    .category {
      display: flex;

      li {
        padding: 5px 10px;
        cursor: pointer;
        border-radius: 5px;
        transition: all 0.3s;

        &.active {
          background-color: rgb(33,172,55);
          color: #fff;
          border: solid 1px rgb(33,172,55);
        }
      }
    }

    p {
      text-align: center;
      font-size: 24px;
    }
  }

  .wrapper {
    display: flex;
    height: calc(100% - 120px);

    .table {
      width: 50%;
      height: 100%;
      border-top: solid 1px #333;

      &.table1 {
        .cell {
          border-left: solid 1px #333;

        }
      }
    }

    .cell {
      cursor: pointer;
      position: relative;
      height: 10%;
      display: block;
      border-right: solid 1px #333;
      border-bottom: solid 1px #333;

      .l {
        text-align: left;
        position: absolute;
        top: 50%;
        left: 20px;
      }

      .r {
        text-align: right;
        position: absolute;
        right: 20px;
        top: 50%;
        i {
          font-style: normal;
          font-size: 18px;
        }
      }

      div {
        transform: translateY(-50%);
        span {
          font-size: 200%;
          line-height: 36px;
          display: block;
          margin-top: auto;
          margin-bottom: auto;
          font-weight: bold;
        }
      }
    }
  }
</style>